import React, { useState, useEffect } from 'react'
import './style/style.css'
import Player from './Radioplayer'
import radios from './db.json'


function RadioApp() {
  //  const [radios, setRadios] = useState([]);
    const [selectedRadio, setSelectedRadio] = useState([])

    // useEffect(() => {
    //     fetch('http://localhost:4000/radios')
    //         .then(res => res.json())
    //         .then(data => {
    //             setRadios(data)
    //         })
    // }, [])


    const radioSelect = (radio) => {
        setSelectedRadio(radio)
    }

    return (
        <>
            <div className="container p-1">
                <div className="column center text-light radiohead">
                    <img src="/logo.gif" width="100%" />
                </div>
                <div className="column add-bottom ">
                    <div id="mainwrap" classsName="p-2">

                        <div id="audiowrap" className="p-3">
                            <div id="audio0">
                                <Player selectedradio={selectedRadio} />
                            </div>

                        </div>
                        <div id="plwrap" className="text-light">
                            <ul id="plList">
                                {
                                    radios.radios.map((radio) => (
                                        <li key={radio.id} classsName="plSel bg-secondary" style={radio.radio_name === selectedRadio.radio_name ? { color: 'yellow' } : null} onClick={(e) => radioSelect(radio)}>
                                            <div classsName="plItem">
                                                <div classsName="plTitle">{radio.id}. {radio.radio_name}</div>
                                                {
                                                    radio.radio_name === selectedRadio.radio_name ? console.log("plSel bg-dark") : console.log("plSel bg-secondary")
                                                }
                                            </div>
                                        </li>
                                    ))
                                }

                            </ul>
                        </div>
                    </div>
                </div>

            </div>
        </>
    )
}

export default RadioApp
